import { Message as SyrenityMessage } from "syrenity/build";
import "../../styles/message.css";
import UserProfilePicture from "./UserProfilePicture";

export default function Message(props: { message: SyrenityMessage }) {
    return (
        <div className="message-container">
            <UserProfilePicture user={props.message.author}/>
            <div className="message-content-container">
                <div className="message-header">
                    <label className="message-header-author">
                        {props.message.author.username || "Loading..."}
                    </label>
                    
                    <small className="message-header-timestamp">
                        {props.message.createdAt.toLocaleDateString()}
                    </small>
                </div>

                <div className="message-content">
                    {props.message.content} ({props.message.id})
                </div>
            </div>
        </div>
    );
}