import Base from "./Base";
export default class File extends Base {
    id;
    fileName;
    createdAt;
    constructor(client, details) {
        super(client);
        this.id = details.id;
        this.fileName = details.file_name;
        this.createdAt = new Date(details.created_at);
    }
    get url() {
        return (`http${this.client.options.api.useSecure ? "s" : ""}://${this.client.options.api.host}/files/${this.id}/${encodeURIComponent(this.fileName)}`);
    }
    get syrenityUrl() {
        return (`syrenity-file://${this.id}`);
    }
}
